
import React, { Component,Fragment } from 'react';
import Search from '../../components/UI/Search/Search.js';
import Movie from '../../components/Movies/Movie/Movie.js';
import {Button} from 'reactstrap';
import axios from 'axios';
import Modal from '../../components/UI/Modal/Modal.js';
import {connect} from 'react-redux';
import {searchMoviesFromDB,cleanAllData, getPopularMovies} from '../../store/movieSearcher.js';
import EditMovie from '../../components/EditMovie/EditMovie.js';
import withErrorHandler from '../withErrorHandler/withErrorHandler.js';






/**
 * MovieSearcher The Main Container, All Logic Have To Be Here!
    *****************************************************
  Firebase API KEY => AIzaSyDrWb1XbSkt1a-QIzxDBWCiyPMfN-Zp4Sc
  FirebaseDB URL => https://movie-searcher-e3abe.firebaseio.com/   do not forget to add .json!
   ***********************************************************************************************
  Movies request url https://api.themoviedb.org/3/search/movie?api_key=70037eb6c1b2400133d1c709f9d0dd6f&query=       query == params that i want to search
  http://www.omdbapi.com/?i=tt3896198&apikey=dbf50537 === another movie db a
 */

export class MovieSearcher extends Component {

state ={
  movieName:'',
  open:false
}

componentWillMount() {
  this.props.getPopularMovies();
}
componentWillUnmount() {
  this.props.cleanAllData();
}



  // this func listen to event and that happend at Search Component and binding to current state!
    searchMovieHandler = (event) => {

        let searchingKey =  event.target.value;
        searchingKey = searchingKey.replace(/[^a-zA-Z ]/g, "");
        searchingKey = searchingKey.toLowerCase()
          .split(' ')
          .map((s) => s.charAt(0).toUpperCase() + s.substring(1))
          .join(' ');
      this.setState({
          movieName: searchingKey
      });
      if(this.props.clicked) {
        this.props.cleanAllData();
      }


}

modalOpener = () => {
  this.setState({
    open:true
  });

}

closeModal = () =>{
  this.setState({
    open:false
  })
}


    render (){

      return (
        <Fragment >

          <div style={{display:'flex'}}>
            <Search changed={(event) => this.searchMovieHandler(event)}
            />
          <Button style={{marginTop:"18px" ,
              backgroundColor: "#6D0301",
            color: "white",
            boxSizing: "border-box",
            textAlign:'center',
            alignItems:'center',
            display:'block',
            width:'20%'
          }}
              clicked={this.props.clicked}
              onClick={()=> this.props.searchMoviesFromDB(this.state.movieName)}
          >Search</Button>
      </div>


        <Movie
          movies={this.props.movies}
          modalClicked={this.modalOpener}
          movie={this.props.movie || this.props.editedMovie} />


        <Modal show={this.state.open} modalClosed={this.closeModal}>
            <EditMovie movies={this.props.movies} movie={this.props.movie || this.props.editedMovie} modalClosed={this.closeModal} />
        </Modal>



        </Fragment>
      );
    }

}


const mapStateToProps = state =>{
    return {
      movies:state.movies,
      clicked:state.clicked,
      movie:state.movie

  }
};


export default connect(mapStateToProps,{searchMoviesFromDB,cleanAllData,getPopularMovies})(withErrorHandler(MovieSearcher,axios));
